import { useState, useContext } from "react"
import loginService from '../services/login'
import userService from '../services/users'
import postService from '../services/posts'
import { useLocation, useNavigate, Link } from "react-router-dom"
import Button from "./Button"
import FormField from "./FormField"
import { UserContext } from "../contexts/userContext"

export const LoginForm = () => {

    const { state } = useLocation()
    const navigate = useNavigate()
    const isSignup = state?.isSignup || false;
    const { setNewUser, setCannotPost } = useContext(UserContext);

    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [name, setName] = useState('')

    const handleLogin = async (event) => {
        event.preventDefault()
        try {
            const user = await loginService.login({ username, password })
            postService.setToken(user.token)
            window.localStorage.setItem('user', JSON.stringify(user))
            setNewUser(user)
            userService.checkCannotPost(user.username)
                .then(boolean => setCannotPost(boolean))
            setUsername('')
            setPassword('')
            navigate(state?.redirectTo || '/')
        } catch (error) {
            console.error(error)
            console.log('wrong credentials')
        }
    }

    const handleRegistration = async (event) => {
        event.preventDefault()
        try {
            await userService.create({ username, password, name })
            setUsername('')
            setPassword('')
            setName('')
            navigate('/enter')
        } catch (error) {
            console.log('something went wrong')
        }
    }

    return (
        <div className="p-4 sm:p-10 bg-almond border border-black rounded-md w-72 sm:w-96 z-20">
            <form onSubmit={isSignup ? handleRegistration : handleLogin}>
                {isSignup &&
                    <FormField
                        label={'Name'}
                        htmlFor={'name'}
                        inputValue={name}
                        inputOnChange={({ target }) => setName(target.value)}
                    />
                }
                <FormField
                    label={'Username'}
                    htmlFor={'username'}
                    inputValue={username}
                    inputOnChange={({ target }) => setUsername(target.value)}
                />
                <FormField
                    label={'Password'}
                    htmlFor={'password'}
                    inputType={'password'}
                    inputValue={password}
                    inputOnChange={({ target }) => setPassword(target.value)}
                />
                <Button type='submit' text={isSignup ? 'Sign up' : 'Log in'} />

                <p className='text-sm mt-4'>
                    {isSignup ? 'Already have an account? ' : 'Not yet a member? '}
                    <Link to='/enter' state={{ isSignup: isSignup ? false : true }}>
                        <span className='hover:underline'>{isSignup ? 'Log in' : 'Sign up'}</span>
                    </Link>
                </p>
            </form>
        </div>
    )
}