import DashboardPost from '../components/DashboardPost';
import dayjs from 'dayjs';
import Button from '../components/Button';
import { useContext } from 'react';
import { PostContext } from '../contexts/postContext';
import { UserContext } from "../contexts/userContext"

const Dashboard = () => {

    const { user } = useContext(UserContext);
    const { posts, openModal } = useContext(PostContext);
    const postsToDisplay = posts.filter(post => post.author.username === user.username)

    if (!user) return <div>loading</div>

    console.log(user)

    return (
        <div className="w-screen flex flex-col p-8 sm:flex-row sm:p-20 justify-between gap-4 sm:gap-16 h-[calc(100vh-60px)]">
            {
                !postsToDisplay.length
                    ? <div className='flex flex-col items-center m-auto'>
                        <div className='mb-6'>You haven't posted yet.</div>
                        {<Button text={'Post your first photo'} onClick={openModal} />}
                    </div>
                    :
                    <div className='flex-1'>
                        <PostsList user={user} posts={postsToDisplay} />
                    </div>
            }
        </div>
    )

}

export default Dashboard

const PostsList = ({ user, posts }) => {
    const yearsPosted = Array.from(new Set(posts.map(post => dayjs(post.createdAt).format('YYYY'))))

    return (
        <div id='posts-list' className='w-full'>
            {
                yearsPosted.map(year => <PostsByYear key={year} year={year} posts={posts} />)
            }
        </div>
    )
}

const PostsByYear = ({ year, posts }) => {

    const { openModal } = useContext(PostContext);

    const postsInYear = posts
        .filter(post => dayjs(post.createdAt).format('YYYY') === year)
        .sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt))
    const currentYear = dayjs().format('YYYY')
    const currentMonthYear = dayjs().format('MM-YYYY')
    const postedThisMonth = posts.find(post => dayjs(post.createdAt).format('MM-YYYY') === currentMonthYear)

    return (
        <div className='flex flex-col w-full sm:w-fit mb-12 m-auto'>
            <h2 className='text-xl font-bold ml-auto mb-6'>{year}</h2>
            <div className='grid grid-cols-1 sm:grid-cols-3 gap-4 sm:gap-8 sm:w-fit w-full'>
                {(year === currentYear && !postedThisMonth) &&
                    <div className='h-36 sm:mt-7 w-full sm:w-64 border border-black rounded-md relative'>
                        <div className="position absolute inset-0 flex flex-col items-center justify-center">
                            <Button text={'Post'} onClick={() => openModal('new')} />
                        </div>
                    </div>
                }
                {postsInYear.map(post => <DashboardPost key={post.id} post={post} />)}
            </div>
        </div>
    )

}