import * as dayjs from 'dayjs'
import Avatar from 'react-avatar'
import { Link } from 'react-router-dom'
import LazyLoadImage from './LazyLoadImage'

const Post = ({ post, isFeed }) => {

    const postMonthYear = dayjs(post.createdAt).format('MMMM YYYY')

    return (
        <div id={post.id} className='w-screen h-screen flex flex-col mb-16'>
            <LazyLoadImage
                className='flex-grow object-contain w-full relative'
                src={post.imageLocation}
            />
            <div className='flex flex-col justify-center text-sm p-6 pb-8 gap-4'>
                {isFeed &&
                    <Link to={`/u/${post.author.username}`}>
                        <div id='author-container' className='flex items-center gap-2'>
                            <Avatar name={post.author.name} size={"28"} round={"6px"} color='#0B0A07' />
                            <p className='hover:underline'>{post.author.name}</p>
                        </div>
                    </Link>
                }
                <p className='text-black leading-relaxed'>
                    <span className='mr-4 opacity-70'>{postMonthYear}</span>
                    {post.caption}
                </p>
            </div>
        </div>
    )
}

export default Post