import axios from 'axios'
import dayjs from 'dayjs'
const baseUrl = '/api/users'

const getAll = () => {
    const request = axios.get(baseUrl)
    return request.then(response => response.data)
}

const getByUsername = (username) => {
    const request = axios.get(`${baseUrl}/${username}`)
    return request.then(response => response.data)
}

const create = (userObject) => {
    const request = axios.post(baseUrl, userObject)
    return request.then(response => response.data)
}

const checkCannotPost = async (username) => {
    const user = await getByUsername(username)
    const yearMonth = (date) => {
        return dayjs(date).format('YYYY-MM')
    }
    return user[0].posts.find(post => yearMonth(post.createdAt) === yearMonth(dayjs()))
}

// eslint-disable-next-line import/no-anonymous-default-export
export default {
    getAll,
    getByUsername,
    create,
    checkCannotPost
}