import { Link } from 'react-router-dom'
import Button from './Button'
import Avatar from 'react-avatar';
import { useEffect, useState, useContext, useRef } from 'react';
import { PostContext } from '../contexts/postContext';
import { UserContext } from "../contexts/userContext"
import UserDropdown from './UserDropdown';

const Header = ({ handleLogout }) => {

    const [dropdownOpen, setDropdownOpen] = useState(false)
    const { openModal } = useContext(PostContext);
    const { user } = useContext(UserContext)
    const avatarRef = useRef();

    useEffect(() => {
        setDropdownOpen(false)
    }, [])

    const handleAvatarClick = (e) => {
        console.log('clicked')
        setDropdownOpen(!dropdownOpen)
    }

    return (
        <header className="h-16 p-4 flex items-center justify-between border border-black">
            <Link to={user ? '/feed' : '/'}><span className="font-extrabold text-2xl -tracking-[.1rem] text-black">focal</span></Link>
            <div className="flex items-center">
                {!user
                    ? <>
                        <Link to="enter">
                            <p className="text-sm mr-6 hover:underline">Log in</p>
                        </Link>
                        <Link to="enter" state={{ isSignup: true }}>
                            <Button text='Sign up' />
                        </Link>
                    </>
                    :
                    <>
                        <Button text={'Post'} onClick={openModal} />
                        <span className='relative'>
                            <div ref={avatarRef} id='avatar'>
                                <Avatar name={user.name} size="32" color='#0B0A07' round={"6px"} onClick={handleAvatarClick} textSizeRatio={2} style={{ position: 'relative', marginLeft: '8px' }} />
                            </div>
                            {
                                dropdownOpen &&
                                <UserDropdown
                                    handleLogout={handleLogout}
                                    isOpen={dropdownOpen}
                                    setIsOpen={setDropdownOpen}
                                    avatarRef={avatarRef}
                                />
                            }
                        </span>
                    </>
                }
            </div>
        </header>
    )
}

export default Header