import { useEffect, useContext } from 'react';
import postService from './services/posts'
import userService from './services/users';
import './App.css';
import PrivateRoutes from './utils/PrivateRoutes';
import { Login } from './routes/Login'
import { Home } from './routes/Home'
import { Feed } from './routes/Feed'
import Dashboard from './routes/Dashboard';
import { NotFound } from './routes/NotFound';
import Modal from './components/Modal';
import { Profile } from './routes/Profile';
import { Layout } from './components/Layout';
import { Routes, Route } from 'react-router-dom'
import { UserContext } from './contexts/userContext';
import { PostContext } from './contexts/postContext';

function App() {
  const { user, setNewUser, setCannotPost } = useContext(UserContext);
  const { setAllPosts, modalOpen, closeModal, modalAction } = useContext(PostContext);

  useEffect(() => {
    postService.getAll()
      .then(initialPosts => {
        setAllPosts(initialPosts)
      })
  }, [])

  useEffect(() => {
    const userInLocalStorage = window.localStorage.getItem('user')
    if (userInLocalStorage) {
      const user = JSON.parse(userInLocalStorage)
      postService.setToken(user.token)
      setNewUser(user)
      userService.checkCannotPost(user.username)
        .then(post => setCannotPost(post))
    }
  }, [])

  return (
    <>
      <Layout user={user} >
        <Routes>
          <Route element={<PrivateRoutes />}>
            <Route path="/feed" element={<Feed />} />
            {user && <Route path="/dashboard" element={<Dashboard />} />}
          </Route>
          <Route path="/" element={user ? <Feed /> : <Home />} />
          <Route path="/enter" element={<Login />} />
          <Route path="/u/:username" element={<Profile />} />
          <Route path="*" element={<NotFound />} />
        </Routes>
      </Layout>
      {modalOpen && <Modal action={modalAction} onClose={closeModal} />}
    </>
  )
}

export default App;
