import { createContext, useState } from 'react';
import { useSearchParams } from 'react-router-dom';

const PostContext = createContext();

const PostProvider = (props) => {
    const [posts, setPosts] = useState([]);
    const [searchParams, setSearchParams] = useSearchParams();
    const [modalOpen, setModalOpen] = useState(searchParams.get('post') === 'new')
    const [modalAction, setModalAction] = useState('new')

    const setAllPosts = (posts) => {
        setPosts(posts);
    };

    const openModal = (action, post = null) => {
        setModalOpen(true);
        setModalAction(post ? { type: action, post } : action)
        searchParams.set("post", action)
        setSearchParams(searchParams)
    };

    const closeModal = (e) => {
        if (e?.target) {
            if (!['modal-overlay', 'modal-close'].includes(e.target.id)) return
        }
        setModalOpen(false);
        searchParams.set("post", 'false')
        setSearchParams('')
    };

    return (
        <PostContext.Provider value={{ posts, setAllPosts, modalOpen, openModal, closeModal, modalAction }}>
            {props.children}
        </PostContext.Provider>
    );
};

export { PostContext, PostProvider };

