import { useState, useContext } from "react"
import postService from '../services/posts'
import { useNavigate, useLocation } from "react-router-dom"
import NewPost from "./NewPost"
import EditPost from "./EditPost"
import { PostContext } from "../contexts/postContext"
import { UserContext } from "../contexts/userContext"

export const PostForm = ({ action, closeModal }) => {

    const { user } = useContext(UserContext);
    const { posts, setAllPosts } = useContext(PostContext);
    const [caption, setCaption] = useState(action.type === 'edit' ? action.post.caption : '')
    const [file, setFile] = useState(null)
    const [imagePreview, setImagePreview] = useState(null)
    const [progress, setProgress] = useState(null)
    const [success, setSuccess] = useState(false)

    const navigate = useNavigate()
    const location = useLocation()

    const addPost = async (e) => {
        e.preventDefault()
        const postObject = {
            caption,
            imageLocation: file
        }
        const newPost = await postService.create(postObject, setProgress)
        setAllPosts([...posts, newPost])
        setTimeout(() => {
            setSuccess(true)
        }, 1000)
        closeModalAndNavigate()
    }

    const updatePost = async (e) => {
        e.preventDefault()
        const postObject = {
            caption
        }
        const updatedPost = await postService.update(action.post.id, postObject, setProgress)
        setAllPosts([...posts].map(post => post.id === updatedPost.id ? updatedPost : post))
        setTimeout(() => {
            setSuccess(true)
        }, 1000)
        closeModalAndNavigate()
    }

    const deletePost = async (id) => {
        await postService.deletePost(id)
        setAllPosts([...posts].filter(post => post.id !== id))
        closeModalAndNavigate()
    }

    const closeModalAndNavigate = () => {
        setTimeout(() => {
            setCaption('')
            setFile(null)
            setImagePreview(null)
            closeModal()
            setProgress(null)
            setSuccess(false)
            navigate(location.pathname === `/u/${user.username}` ? 0 : `/u/${user.username}`)
        }, 4000)
    }

    const handleCaptionChange = (event) => {
        setCaption(event.target.value)
    }

    const handleFileChange = (e) => {
        const imageFile = e.target.files[0];
        setImagePreview(URL.createObjectURL(imageFile))
        const reader = new FileReader();
        reader.onloadend = () => {
            setFile(reader.result);
        };
        reader.readAsDataURL(imageFile);
    }

    const handleRemove = () => {
        setFile(null)
        setImagePreview(null)
    }

    return (
        action.type === 'edit'
            ? <EditPost
                post={action.post}
                handleCaptionChange={handleCaptionChange}
                caption={caption}
                updatePost={updatePost}
                progress={progress}
                setProgress={setProgress}
                success={success}
                handleDelete={deletePost}
            />
            : <NewPost
                addPost={addPost}
                file={file}
                handleFileChange={handleFileChange}
                imagePreview={imagePreview}
                progress={progress}
                success={success}
                handleRemove={handleRemove}
                handleCaptionChange={handleCaptionChange}
                caption={caption}
            />
    )
}