const Button = ({ text, onClick, type = 'button', secondary }) => {

    const buttonStyle = !secondary
        ? ' bg-black text-white'
        : 'bg-none text-black border border-black'

    return (
        <button
            type={type}
            onClick={onClick} className={`px-5 py-1.5 text-sm rounded-md ${buttonStyle}`}>{text}</button>
    )

}

export default Button