import React, { useRef, useState, useEffect } from 'react';
import { getTransformationURL } from '../utils/getTransformationURL';

function LazyLoadImage({ className, src, small = false, children }) {
    const [isIntersecting, setIsIntersecting] = useState(false);
    const ref = useRef();

    useEffect(() => {
        const observer = new IntersectionObserver(
            ([entry]) => {
                if (entry.isIntersecting) {
                    setIsIntersecting(true);
                    observer.disconnect();
                }
            },
            {
                rootMargin: '0px 0px 300px 0px',
                threshold: 0
            }
        );

        observer.observe(ref.current);

        return () => {
            observer.disconnect();
        };
    }, []);

    const placeholder = getTransformationURL(src, small, 'blur')

    return (
        <div
            ref={ref}
            className={className}
            style={{
                backgroundImage: isIntersecting ? `url(${getTransformationURL(src, small)})` : `url(${placeholder})`,
                backgroundSize: 'cover',
                backgroundPosition: 'center'
            }}
        >
            {children}
        </div>
    );
}

export default LazyLoadImage;