import { useEffect, useContext } from "react";
import { PostForm } from "./PostForm";
import { UserContext } from "../contexts/userContext";

const Modal = ({ action, onClose }) => {

    const { cannotPost } = useContext(UserContext);

    useEffect(() => {
        document.body.style.overflow = 'hidden'
        return () => {
            document.body.style.overflow = 'auto'; // remove overflow:hidden from body when modal is closed
        };
    }, [])

    return (
        <div id='modal-overlay' className='bg-[#0009] fixed inset-0 sm:p-20 flex items-center justify-center' onClick={onClose}>
            <CloseIcon onClick={onClose} />
            <div className='relative z-5 bg-almond h-full sm:h-fit w-full sm:max-w-3xl rounded-md ml-auto mr-auto border border-black flex items-center justify-center'>
                {cannotPost
                    ? <p className="p-8 sm:p-16 text-center">You've already posted this month. Please come back next month to post again.</p>
                    : <PostForm action={action} closeModal={onClose} />
                }
            </div>
        </div>
    );

}

export default Modal

const CloseIcon = ({ onClick }) =>
    <div id='modal-close' onClick={onClick} className='z-50 sm:z-5 p-4 absolute top-0 left-0'>
        <svg id='modal-close' xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6 text-black sm:text-almond">
            <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
        </svg>
    </div>
