const FormField = ({ label, htmlFor, inputType = 'text', inputValue, inputOnChange }) => {

    return (
        <div>
            <label htmlFor={htmlFor} className="block mb-2 text-xs font-medium text-black">{label}</label>
            <input
                type={inputType}
                value={inputValue}
                onChange={inputOnChange}
                className="bg-almond border border-black text-gray-900 text-sm rounded-md block w-full p-1.5 mb-4 outline-none"
            />
        </div>
    )

}

export default FormField