import { HashLink } from "react-router-hash-link"
import LazyLoadImage from "./LazyLoadImage"
import dayjs from "dayjs"
import { useState, useContext } from "react"
import postService from "../services/posts"
import { PostContext } from '../contexts/postContext'

const DashboardPost = ({ post }) => {

    const [isHovered, setIsHovered] = useState(false)
    const [deletePressed, setDeletePressed] = useState(false)
    const overlayOpacity = isHovered ? 'opactity-100' : 'opacity-0'
    const { posts, setAllPosts, openModal } = useContext(PostContext);

    const handleMouseLeave = () => {
        setIsHovered(false)
        setTimeout(() => {
            setDeletePressed(false)
        }, 300)
    }

    const handleDelete = async (id) => {
        await postService.deletePost(id)
        setAllPosts([...posts].filter(post => post.id !== id))
    }

    return (
        <div onMouseEnter={() => setIsHovered(true)} onMouseLeave={() => handleMouseLeave()}>
            <div className="mb-2 "><p className="font-medium text-lg sm:text-sm text-black opacity-80 text-right">{dayjs(post.createdAt).format('MMM')}</p></div>
            <LazyLoadImage
                className={"rounded-md border border-black h-36 w-full sm:w-64 relative"}
                src={post.imageLocation}
                small={true}
            >
                <div className={`position absolute inset-0 bg-[#00000077] flex items-center justify-center ${overlayOpacity} transition-opacity duration-300 ease-linear`}>
                    <div className="flex gap-4">
                        {deletePressed ? <ConfirmIcon onClick={() => handleDelete(post.id)} /> : <EditIcon onClick={() => openModal('edit', post)} />}
                        <DeleteIcon onClick={() => setDeletePressed(!deletePressed)} />
                        {deletePressed ? <CancelIcon onClick={() => setDeletePressed(false)} /> : <LinkIcon post={post} />}
                    </div>
                </div>
            </LazyLoadImage>
        </div>
    )

}

export default DashboardPost

const EditIcon = ({ onClick }) =>
    <div onClick={onClick}>
        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6 text-almond">
            <path strokeLinecap="round" strokeLinejoin="round" d="M16.862 4.487l1.687-1.688a1.875 1.875 0 112.652 2.652L6.832 19.82a4.5 4.5 0 01-1.897 1.13l-2.685.8.8-2.685a4.5 4.5 0 011.13-1.897L16.863 4.487zm0 0L19.5 7.125" />
        </svg>
    </div>

const DeleteIcon = ({ onClick }) =>
    <div onClick={onClick}>
        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6 text-almond">
            <path strokeLinecap="round" strokeLinejoin="round" d="M14.74 9l-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 01-2.244 2.077H8.084a2.25 2.25 0 01-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 00-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 013.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 00-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 00-7.5 0" />
        </svg>
    </div>


const LinkIcon = ({ post }) =>
    <HashLink to={`/u/${post.author.username}#${post.id}`}>
        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6 text-almond">
            <path strokeLinecap="round" strokeLinejoin="round" d="M13.5 6H5.25A2.25 2.25 0 003 8.25v10.5A2.25 2.25 0 005.25 21h10.5A2.25 2.25 0 0018 18.75V10.5m-10.5 6L21 3m0 0h-5.25M21 3v5.25" />
        </svg>
    </HashLink>

