const FileInput = ({ text, onChange, secondary }) => {

    const labelStyle = !secondary
        ? ' bg-black text-white'
        : 'bg-none text-black border border-black'

    return (
        <>
            <label htmlFor='file-input' className={`px-5 py-1.5 text-sm rounded-md ${labelStyle}`}>
                {text}
            </label>
            <input id="file-input" type="file" onChange={onChange} accept='image/*' className="hidden" />
        </>
    )

}

export default FileInput