import FileInput from "./FileInput"
import FormField from "./FormField"
import Button from "./Button"
import dayjs from "dayjs"

const NewPost = ({ addPost, file, handleFileChange, imagePreview, progress, success, handleRemove, handleCaptionChange, caption }) => {

    const formContainerSizing = imagePreview ? 'w-full h-fit' : 'w-64'

    return (
        <div className={`p-4 sm:p-10 bg-almond rounded-md transition-[width] duration-1000 ease-in-out max-w-3xl ${formContainerSizing}`}>
            <form onSubmit={addPost} className='sm:mt-0 mt-12'>
                {
                    !file
                        ?
                        <div className="h-full w-full flex justify-center items-center">
                            <FileInput text={'Choose photo'} onChange={handleFileChange} />
                        </div>
                        :
                        <div className="h-full w-full flex flex-col items-start">
                            <div className="w-full">
                                <h3 className="font-bold mb-4 text-lg">{dayjs().format('MMMM YYYY')}</h3>
                                <label className="block mb-2 text-xs font-medium text-black">Photo</label>
                                <div
                                    className='h-72 sm:h-72 object-contain relative rounded-md mb-2 border border-black' alt={imagePreview}
                                    style={{
                                        backgroundImage: `url(${imagePreview})`,
                                        backgroundSize: 'cover',
                                        backgroundPosition: 'center'
                                    }}>
                                    <div
                                        id='progress-bar'
                                        className={'absolute inset-0'}
                                        style={{
                                            width: progress ? `${progress}%` : '0%',
                                            backgroundColor: success ? '#21af5c70' : '#F7E9DF99',
                                            transition: 'background-color 1000ms ease-in, width 4000ms ease-out'
                                        }}
                                    >

                                    </div>
                                </div>
                            </div>
                            <div className="flex self-end items-center">
                                <p className='text-sm hover:underline mr-4' onClick={() => handleRemove()}>Remove</p>
                                <FileInput secondary text={'Change'} onChange={handleFileChange} />
                            </div>
                            <div className="w-full">
                                <FormField label='Caption' htmlFor={'caption'} inputValue={caption} inputOnChange={handleCaptionChange} />
                            </div>
                            <div className="flex self-end">
                                <Button text='Upload' type='submit' />
                            </div>
                        </div>
                }
            </form>
        </div>
    )
}

export default NewPost