export const getTransformationURL = (cloudinaryURL, small = false, blur = false) => {

    let toInsert = 'q_auto:low,w_2000'
    if (small) toInsert += ',w_500,h_500,c_fill'
    if (blur) toInsert += ',e_blur:1000'
    toInsert = toInsert.replace(/,+$/, ''); // remove any trailing commas

    const pattern = /(upload\/)/g;

    const modifiedString = cloudinaryURL.replace(pattern, `upload/${toInsert}/`);
    return modifiedString
}