import { useParams } from 'react-router-dom'
import { useEffect, useState, useContext } from 'react'
import userService from '../services/users'
import Post from '../components/Post'
import Avatar from 'react-avatar';
import Button from '../components/Button';
import { PostContext } from '../contexts/postContext';
import { UserContext } from '../contexts/userContext'
import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'

export const Profile = () => {
    const { username: requestedUsername } = useParams() //destructuring with assignment to new variable
    const [user, setUser] = useState(null)
    const { openModal } = useContext(PostContext)
    const { user: loggedInUser } = useContext(UserContext)
    const [ownProfile, setOwnProfile] = useState(null)

    useEffect(() => {
        userService.getByUsername(requestedUsername)
            .then(user => {
                setUser(user[0])
                setOwnProfile(loggedInUser.username === user[0].username)
            })

    }, [requestedUsername, loggedInUser?.username])

    if (!user) return (
        <div className={'w-screen h-[calc(100vh-60px)] flex flex-col'}>
            <div className='h-80 flex justify-center items-center'>
                <div id='pendinguserinfo' className='flex flex-col items-center'>
                    <SkeletonTheme baseColor="#202020" highlightColor="#444">
                        <Skeleton width={48} height={48} style={{ marginBottom: '8px' }} />
                        <Skeleton width={150} height={24} style={{ marginTop: '16px' }} />
                        <Skeleton width={120} height={24} style={{ marginTop: '14px' }} />
                    </SkeletonTheme>
                </div>
            </div>
        </div>

    )

    return (
        <div className={!user.posts.length && 'w-screen h-[calc(100vh-60px)] flex flex-col'}>
            <div className='h-80 flex justify-center items-center'>
                <div id='userinfo' className='flex flex-col items-center'>
                    <Avatar name={user.name} size={48} round={6} color='#0B0A07' style={{ marginBottom: '16px' }} />
                    <h1 className='font-bold text-2xl mb-2'>{user.name}</h1>
                    <p className='text-lg'>{`@${user.username}`}</p>
                </div>
            </div>
            {
                !user.posts.length
                    ? <div className='flex flex-col items-center'>
                        <div className='mb-6'>{user.name} hasn't posted yet.</div>
                        {ownProfile && <Button text={'Post your first photo'} onClick={openModal} />}
                    </div>

                    : <div>
                        {
                            user.posts
                                .sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt))
                                .map(post => <Post post={post} key={post.id} />)
                        }
                    </div>
            }
        </div>
    )
}
