import { useNavigate, Link } from "react-router-dom"
import Header from "./Header"
import * as dayjs from 'dayjs'
import { useContext } from "react";
import { UserContext } from "../contexts/userContext"

export const Layout = ({ children }) => {

    const { user, setNewUser } = useContext(UserContext);
    const navigate = useNavigate()

    const handleLogout = () => {
        window.localStorage.removeItem('user')
        setNewUser(null)
        navigate('/')
    }

    return (
        <div id='wrapper' className="flex flex-col bg-almond min-h-screen">
            <Header handleLogout={handleLogout} />
            <div id='page-content' className="border border-black border-t-0 flex-grow h-full relative">
                {children}
            </div>
            <footer className="bg-black p-6">
                <Link to={user ? '/feed' : '/'}><span className="font-extrabold text-2xl -tracking-[.1rem] text-almond">focal</span></Link>
                <p className="text-almond text-sm font-medium mt-4">Under-sharing.</p>
                <p className="text-almond opacity-50 text-sm mt-6">{dayjs().year()} focal · all rights reserved</p>
            </footer>
        </div>
    )
}