import { Navigate, Outlet, useLocation } from 'react-router-dom'
const PrivateRoutes = () => {

    let auth = JSON.parse(window.localStorage.getItem('user')) ?? false
    const location = useLocation()
    return (
        auth.token ? <Outlet /> : <Navigate to='/enter' state={{ redirectTo: location.pathname }} />
    )
}

export default PrivateRoutes